<template>
  <ul class="right-list-container">
    <li v-for="(item, i) in list" :class="{active: item.isSelect}" :key="i">
      <span :class="{active: item.isSelect}" @click="handleClick(item)">{{item.name}}</span>
      <span v-if="item.aside" class="aside" :class="{active: item.isSelect}">{{item.aside}}</span>
      <RightList :list="item.children" @select="handleClick"></RightList>
    </li>
  </ul>
</template>

<script>
export default {
  name: "RightList",
  props:{
    list:{
      type: Array,
      default: ()=>[]
    }
  },
  methods:{
    handleClick(item) {
      if (!item.isSelect) {
        this.$emit("select", item);
      }
    }
  }
}
</script>

<style scoped lang="less">
@import "~@/styles/var";
  .right-list-container{
    list-style: none;
    padding: 0;
    .right-list-container{
      margin-left: 1em;
    }
    li{
      span{
        cursor: pointer;
      }
      min-height: 40px;
      line-height: 40px;
      .active{
        color: @warn;
        font-weight: bold;
      }
      .aside{
        font-size: 12px;
        margin-left: 1em;
        color: @gray;
        &.active{
          color: @warn;
          font-weight: bold;
        }
      }
    }
  }
</style>